import React, { Suspense, lazy } from 'react';
import { Switch } from 'react-router-dom';
import ErrorBoundary from '@/components/ErrorBoundary';
import LoadingPage from '@/components/LoadingPage';
import RouterProxy from '@/components/RouterProxy';

const Home = lazy(() => import('../pages/Home'));
const Login = lazy(() => import('../pages/Login'));
const Register = lazy(() => import('../pages/Login/Register'));
const Complete = lazy(() => import('../pages/Login/Complete'));
const CompleteAll = lazy(() => import('../pages/Login/CompleteAll'));
const CompleteShow = lazy(() => import('../pages/Login/CompleteShow'));

const Routes = (): React.ReactElement => {
  return (
    <ErrorBoundary>
      <Suspense fallback={<LoadingPage />}>
        <Switch>
          <RouterProxy
            exact
            path="/login"
            component={Login}
            title="菓子题库登录"
          />
          <RouterProxy
            exact
            path="/register"
            component={Register}
            title="菓子题库注册"
          />
          <RouterProxy
            exact
            path="/complete"
            component={Complete}
            title="新用户登录"
          />
          <RouterProxy
            exact
            path="/complete-all"
            component={CompleteAll}
            title="新用户登录"
          />
          <RouterProxy
            exact
            path="/complete-show"
            component={CompleteShow}
            title="新用户登录"
          />
          <RouterProxy
            exact
            path="/library"
            component={Home}
            title="菓子题库"
          />
          <RouterProxy exact path="/find" component={Home} title="菓子发现" />
          <RouterProxy
            exact
            path="/user-center"
            component={Home}
            title="菓子与我"
          />
        </Switch>
      </Suspense>
    </ErrorBoundary>
  );
};

export default Routes;
